
import { getLogin,getDoneTask } from '../../service/service.js'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default{
data(){
return{
doneList:[],
mescroll:null,
show:true,
list:[]

}
},
components: {
InlineLoading
},
methods:{
goDetail(param){
this.$router.push({path:"/finish/done_detail",query:{
data:param
}})
}




























},
created(){
var token = localStorage.getItem("token");
getDoneTask(token).then((res)=>{

this.doneList = res.tableContent;
console.log(res);
})

},
mounted(){
var self = this;
self.mescroll = new MeScroll("finish",{
up:{
isBounce:false,
use:false





},
down:{
use:false
}
})
}

}





