
import {getTask} from '../../service/service.js'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default{
data(){
return{
show:false,
listArr:[],
list:[]
}
},
components: {
InlineLoading
},
methods:{
goDetail(param){
this.$router.push({path:"/to_do/do_detail",query:{
data:param
}})
}
},
mounted(){
var self = this;
self.mescroll = new MeScroll("td",{
up:{
isBounce:false,
use:false

},
down:{
use:false
}

})
},
created(){
var token = localStorage.getItem("token");
getTask(token).then((res)=>{
console.log(res);
this.listArr = res.data.tableContent;
this.$event.$emit("num",res.data.tableContent.length);



})


}


}





